


export default function DropDown({ results, onSelection }) {

    const listItem = results.map((result) =>
        <button key={result.place_id} onClick={() => onSelection(result)}>
            <li className="inline-flex py-2 px-4 w-full hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">
                {result.display_name}
            </li>
        </button >
    )

    return (
        <div className="w-44 float-left bg-white rounded divide-y divide-gray-100 shadow dark:bg-gray-700">
            <ul className="py-1 text-sm text-gray-700 dark:text-gray-200">{listItem}</ul>
        </div>
    )
}