import { useEffect, useState } from 'react';
import { BsSearch, BsXCircle } from "react-icons/bs";
import DropDown from './DropDown';

export default function SearchBar({ onSelection, closeSearch }) {
    const [searchInput, setSearchInput] = useState("");
    const [nominatimResponse, setNominatimResponse] = useState(null);



    async function getCity(input) {
        await fetch(`https://nominatim.openstreetmap.org/search?q=${input}&format=json&limit=4`)
            .then(res => res.json())
            .then(data => setNominatimResponse(data))
    }

    const handleChange = e => setSearchInput(e.target.value)

    const handleSearch = e => {
        getCity(searchInput)
        e.preventDefault()
    }

    const handleX = () => {
        setSearchInput("");
        setNominatimResponse(null);
    }

    useEffect(() => {
        handleX()
    }, [closeSearch])

    useEffect(() => {
        if (!searchInput) {
            setNominatimResponse(null)
        }
    }, [searchInput])

    return (
        <div className='z-10 p-3 w-full items-stretch lg:p-5 top-14 lg:max-w-xl lg:inset-x-5 lg:left-auto lg:top-0 lg:right-0 fixed'>
            <form onReset={handleX} onSubmit={handleSearch} id='searchForm' className=''>
                <div className='flex'>
                    <div className='basis-10/12 inline-block bg-white border-rose-600 border-2 text-gray-900 text-sm rounded-lg p-3'>
                        <input type="search" placeholder="Search neighborhoods or cities..." onChange={handleChange} required className='w-full placeholder:italic px-3 py-2 focus:border-b-2 focus:border-b-rose-600 outline-none text-md lg:text-lg border-transparent'></input>
                    </div>

                    <button type='submit' form='searchForm' className='text-center p-3 ml-2 text-sm text-slate-200 bg-gradient-to-r from-zinc-700 to-rose-600 rounded-lg focus:outline-none'>
                        <BsSearch
                            size={"32px"}
                        />
                    </button>
                </div>
            </form>
            {nominatimResponse && searchInput && < DropDown results={nominatimResponse} onSelection={onSelection} />}
        </div>
    )
}