import { useState } from 'react';
import Map, { NavigationControl, Source, Layer } from 'react-map-gl';
import maplibregl from 'maplibre-gl';
import 'maplibre-gl/dist/maplibre-gl.css';
import { style } from '../utils/style'
import Title from './Title';
import SearchBar from './SearchBar';
import osmtogeojson from "osmtogeojson";
import Spinner from './Spinner'
import InfoBar from './InfoBar'



export default function Main() {
    const [viewState, setViewState] = useState({
        longitude: -98,
        latitude: 40,
        zoom: 3
    });
    const [parkingData, setParkingData] = useState(null);
    const [selectedResult, setSelectedResult] = useState(false);
    const [closeSearch, setCloseSearch] = useState(false);

    function handleSelection(city) {
        setParkingData(null);
        setSelectedResult(true);
        setCloseSearch(true);

        // zoom to city
        setViewState({
            latitude: city.lat,
            longitude: city.lon,
            zoom: 13,
        })

        // call API
        getParking(city.osm_id)
        return
    }

    async function getParking(id) {
        const query = `[out:json][timeout:30];rel(${id});map_to_area -> .a;nwr(area.a)["amenity"="parking"];(._;>;);out;`

        setCloseSearch(false);

        const baseURL = "https://overpass-api.de/api/interpreter?data="
        return await fetch(baseURL + query)
            .then(res => res.json())
            .then(data => {
                setCloseSearch(true);
                setParkingData(osmtogeojson(data));
            })
            .catch(err => {
                console.log(err)
            })
    }


    const layerStyle = {
        id: 'parking',
        type: 'fill',
        paint: {
            'fill-color': 'maroon',
            'fill-opacity': 0.8,
        }
    }

    return (
        <>
            <Title />
            {parkingData && <InfoBar parkingData={parkingData} />}
            <SearchBar
                onSelection={handleSelection}
                closeSearch={closeSearch}
            />
            {selectedResult && !parkingData && <Spinner />}
            <Map
                {...viewState}
                onMove={evt => setViewState(evt.viewState)}
                mapLib={maplibregl}
                style={{ width: '100%', height: '100vh' }}
                mapStyle={style}
            >
                <NavigationControl position='bottom-right' />
                {parkingData &&
                    <Source id='city-parking' type='geojson' data={parkingData}>
                        <Layer {...layerStyle} />
                    </Source>
                }
            </Map>
        </>
    );
}