

export default function Spinner() {
    return (
        <div className="z-30 top-1/3 sm:top-0 p-3 absolute">
            <div className="inline-block sm:flex justify-center sm:w-screen sm:h-screen items-center">
                <p className="font-mono text-center left-1/2 p-5 text-slate-100 text-xl bg-gradient-to-r from-zinc-700 to-rose-600 rounded-lg shadow-lg">Parking lots encroaching...</p>                <div className="p-3 grid h-full place-items-center">
                    <div className="border-t-transparent border-solid animate-spin rounded-full border-blue-400 border-8 h-32 w-32"></div>
                </div>
            </div>
        </div>
    )
}