import { useEffect, useState } from "react";
import area from '@turf/area';

export default function InfoBar({ parkingData }) {
    const [featureCount, setFeatureCount] = useState(0);
    const [parkingSqFt, setParkingSqFt] = useState(0);
    const [parkingSqM, setParkingSqM] = useState(0);
    const [isFeet, setIsFeet] = useState(true);


    useEffect(() => {
        // feature count
        setFeatureCount(parkingData.features.length)

        // sq m
        let area_sq_meters = area(parkingData);
        setParkingSqM(Math.round(area_sq_meters).toLocaleString());

        // convert sq m to sq ft
        let area_sq_feet = Math.round(area_sq_meters * 10.764).toLocaleString();
        setParkingSqFt(area_sq_feet);

    }, [parkingData])


    return (
        <div onClick={() => setIsFeet(!isFeet)} className="p-3 z-10 grow break-words w-fit bg-gradient-to-r from-zinc-700 to-rose-600 rounded-lg shadow-lg fixed inset-x-3 bottom-2">
            {
                isFeet ?
                    <div>
                        <div className="font-mono w-auto h-auto text-5xl text-center text-slate-100">{parkingSqFt}</div>
                        <p className="font-sans text-center text-slate-300">Total square feet</p>
                    </div>
                    :
                    <div>
                        <div className="font-mono w-auto h-auto text-5xl text-center text-slate-100">{parkingSqM}</div>
                        <p className="font-sans text-center text-slate-300">Total square meters</p>
                    </div>
            }
        </div >
    )
}